.feed {
    height: auto;
    position: relative;
    margin: 0;
    width: 100%;

}

.feed-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    width: 100%;
    position: relative;
}


.feed-contents {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 10px;
}


@media only screen and (max-width: 750){
    .feed {
        transform: translateY(85px);
    }
}